<!-- src/pages/kzzx/quanxianguanli.vue -->
<template>
    <div class="permission-management tech-main">
        <div class="content-container tech-grid">
            <!-- 用户列表区域 -->
            <div class="user-list-section tech-card">
                <div class="section-header tech-header">
                    <h3>用户列表</h3>
                    <el-button type="primary" size="small" @click="addUser" class="tech-button">
                        <i class="el-icon-plus"></i> 添加用户
                    </el-button>
                </div>

                <div class="table-container">
                    <el-table :data="users" style="width: 100%" highlight-current-row @current-change="handleUserSelect"
                        class="tech-table">
                        <el-table-column prop="id" label="用户ID" width="80"
                            class-name="tech-table-cell"></el-table-column>
                        <el-table-column prop="username" label="用户名" class-name="tech-table-cell"></el-table-column>
                        <el-table-column prop="role" label="角色" class-name="tech-table-cell">
                            <template #default="scope">
                                <el-tag :type="scope.row.role === 'admin' ? 'danger' : 'primary'" class="tech-tag">
                                    {{ scope.row.role === 'admin' ? '管理员' : '普通用户' }}
                                </el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column label="操作" width="150" class-name="tech-table-cell">
                            <template #default="scope">
                                <div class="action-buttons">
                                    <el-button size="mini" @click.stop="editUser(scope.row)" class="tech-button-mini">
                                        编辑
                                    </el-button>
                                    <el-button size="mini" type="danger" @click.stop="deleteUser(scope.row)"
                                        :disabled="scope.row.role === 'admin' && scope.row.username === 'admin'"
                                        class="tech-button-mini danger">
                                        删除
                                    </el-button>
                                </div>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
            </div>

            <!-- 添加用户区域 -->
            <div class="add-user-section tech-card" v-if="showAddUserForm">
                <div class="section-header tech-header">
                    <h3>添加用户</h3>
                    <el-button type="success" size="small" @click="saveUser" class="tech-button success">
                        保存用户
                    </el-button>
                </div>

                <div class="user-form-content tech-content">
                    <el-form :model="userForm" :rules="userRules" ref="addUserForm" class="tech-form">
                        <el-form-item label="用户名" prop="username">
                            <el-input v-model="userForm.username" autocomplete="off" class="tech-input"></el-input>
                        </el-form-item>
                        <el-form-item label="角色" prop="role">
                            <el-select v-model="userForm.role" placeholder="请选择角色" class="tech-select" @change="handleRoleChange">
                                <el-option label="管理员" value="admin"></el-option>
                                <el-option label="普通用户" value="user"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="密码" prop="password">
                            <el-input v-model="userForm.password" type="password" autocomplete="new-password"
                                class="tech-input"></el-input>
                        </el-form-item>
                        
                        <!-- 楼宇权限设置 (仅对普通用户显示) -->
                        <div v-if="userForm.role === 'user'">
                            <h4 class="tech-subtitle">楼宇访问权限</h4>
                            <div class="building-permissions-container tech-building-container">
                                <el-checkbox 
                                    v-for="building in buildings" 
                                    :key="building.id"
                                    v-model="userForm.buildingPermissions[building.id]" 
                                    :label="building.name"
                                    class="tech-checkbox building-checkbox">
                                    {{ building.name }}
                                </el-checkbox>
                            </div>
                        </div>
                        
                        <!-- 管理员权限说明 -->
                        <div v-else class="admin-permissions-info tech-admin-panel">
                            <el-alert title="管理员拥有系统所有权限" type="warning" description="管理员可以访问所有楼栋和功能模块" show-icon
                                class="tech-alert">
                            </el-alert>
                        </div>
                    </el-form>
                </div>
            </div>

            <!-- 编辑用户区域 -->
            <div class="edit-user-section tech-card" v-else-if="showEditUserForm && editingUser">
                <div class="section-header tech-header">
                    <h3>编辑用户</h3>
                    <el-button type="success" size="small" @click="saveUser" class="tech-button success">
                        更新用户
                    </el-button>
                </div>

                <div class="user-form-content tech-content">
                    <el-form :model="userForm" :rules="userRules" ref="editUserForm" class="tech-form">
                        <el-form-item label="用户名" prop="username">
                            <el-input v-model="userForm.username" autocomplete="off" class="tech-input"></el-input>
                        </el-form-item>
                        <el-form-item label="角色" prop="role">
                            <el-select v-model="userForm.role" placeholder="请选择角色" class="tech-select" @change="handleRoleChange">
                                <el-option label="管理员" value="admin"></el-option>
                                <el-option label="普通用户" value="user"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="密码" prop="password">
                            <el-input v-model="userForm.password" type="password" placeholder="留空则不修改密码" autocomplete="new-password"
                                class="tech-input"></el-input>
                        </el-form-item>
                        
                        <!-- 楼宇权限设置 (仅对普通用户显示) -->
                        <div v-if="userForm.role === 'user'">
                            <h4 class="tech-subtitle">楼宇访问权限</h4>
                            <div class="building-permissions-container tech-building-container">
                                <el-checkbox 
                                    v-for="building in buildings" 
                                    :key="building.id"
                                    v-model="userForm.buildingPermissions[building.id]" 
                                    :label="building.name"
                                    class="tech-checkbox building-checkbox">
                                    {{ building.name }}
                                </el-checkbox>
                            </div>
                        </div>
                        
                        <!-- 管理员权限说明 -->
                        <div v-else class="admin-permissions-info tech-admin-panel">
                            <el-alert title="管理员拥有系统所有权限" type="warning" description="管理员可以访问所有楼栋和功能模块" show-icon
                                class="tech-alert">
                            </el-alert>
                        </div>
                    </el-form>
                </div>
            </div>

            <!-- 用户详情区域 -->
            <div class="user-detail-section tech-card" v-else-if="showUserDetail && selectedUser">
                <div class="section-header tech-header">
                    <h3>{{ selectedUser.username }} 的详细信息</h3>
                </div>

                <div class="user-detail-content tech-content">
                    <div class="user-info-item">
                        <span class="label">用户ID:</span>
                        <span class="value">{{ selectedUser.id }}</span>
                    </div>
                    <div class="user-info-item">
                        <span class="label">用户名:</span>
                        <span class="value">{{ selectedUser.username }}</span>
                    </div>
                    <div class="user-info-item">
                        <span class="label">角色:</span>
                        <span class="value">
                            <el-tag :type="selectedUser.role === 'admin' ? 'danger' : 'primary'" class="tech-tag">
                                {{ selectedUser.role === 'admin' ? '管理员' : '普通用户' }}
                            </el-tag>
                        </span>
                    </div>
                    
                    <!-- 楼栋权限信息 -->
                    <div class="building-permissions-info" v-if="selectedUser.role !== 'admin'">
                        <h4 class="tech-subtitle">楼栋访问权限</h4>
                        <div class="building-permissions-container tech-building-container">
                            <div v-for="building in buildings" :key="building.id" class="building-item">
                                <span class="building-name">{{ building.name }}:</span>
                                <el-tag :type="selectedUser.buildingPermissions[building.id] ? 'success' : 'info'" 
                                        class="permission-tag">
                                    {{ selectedUser.buildingPermissions[building.id] ? '允许访问' : '禁止访问' }}
                                </el-tag>
                            </div>
                        </div>
                    </div>

                    <!-- 管理员权限说明 -->
                    <div class="admin-permissions-info tech-admin-panel" v-else>
                        <el-alert title="管理员拥有系统所有权限" type="warning" description="管理员可以访问所有楼栋和功能模块" show-icon
                            class="tech-alert">
                        </el-alert>

                        <div class="admin-building-list">
                            <h4 class="tech-subtitle">可访问的楼栋</h4>
                            <div class="building-tags">
                                <el-tag v-for="building in buildings" :key="building.id" type="success" effect="dark"
                                    class="tech-tag building-tag">
                                    {{ building.name }}
                                </el-tag>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <!-- 权限设置区域 -->
            <div class="permission-section tech-card" v-else-if="showPermissionSettings && selectedUser">
                <div class="section-header tech-header">
                    <h3>{{ selectedUser.username }} 的权限设置</h3>
                    <el-button type="success" size="small" @click="savePermissions" :loading="saving"
                        class="tech-button success">
                        保存权限
                    </el-button>
                </div>

                <div class="permission-content tech-content">
                    <div class="role-info tech-role-info">
                        <el-tag :type="selectedUser.role === 'admin' ? 'danger' : 'primary'" class="tech-tag">
                            {{ selectedUser.role === 'admin' ? '管理员 - 拥有所有权限' : '普通用户' }}
                        </el-tag>
                    </div>

                    <!-- 楼栋权限列表 -->
                    <div class="building-permissions" v-if="selectedUser.role !== 'admin'">
                        <h4 class="tech-subtitle">楼栋访问权限</h4>
                        <div class="building-permissions-container tech-building-container">
                            <el-checkbox v-for="building in buildings" :key="building.id"
                                v-model="selectedUser.buildingPermissions[building.id]" :label="building.name"
                                :disabled="selectedUser.role === 'admin'" class="tech-checkbox building-checkbox">
                                {{ building.name }}
                            </el-checkbox>
                        </div>
                    </div>

                    <!-- 管理员权限说明 -->
                    <div class="admin-permissions tech-admin-panel" v-else>
                        <el-alert title="管理员拥有系统所有权限" type="warning" description="管理员可以访问所有楼栋和功能模块" show-icon
                            class="tech-alert">
                        </el-alert>

                        <div class="admin-building-list">
                            <h4 class="tech-subtitle">可访问的楼栋</h4>
                            <div class="building-tags">
                                <el-tag v-for="building in buildings" :key="building.id" type="success" effect="dark"
                                    class="tech-tag building-tag">
                                    {{ building.name }}
                                </el-tag>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <!-- 无用户选择提示 -->
            <div class="no-selection tech-card" v-else>
                <el-alert title="请选择一个用户进行权限设置" type="info" description="从左侧用户列表中选择一个用户来管理其权限" show-icon
                    class="tech-alert">
                </el-alert>
            </div>
        </div>

        <right-button-group />
    </div>
</template>

<script>
import RightButtonGroup from '@/components/RightButtonGroup.vue';

export default {
    components: {
        RightButtonGroup,
    },
    name: 'Quanxianguanli',
    data() {
        return {
            selectedButton: '权限管理',
            // 用户列表数据
            users: [
                {
                    id: 1,
                    username: 'admin',
                    role: 'admin',
                    buildingPermissions: {}
                },
                {
                    id: 2,
                    username: '用户一',
                    role: 'user',
                    buildingPermissions: {
                        'building-a': true,
                        'building-b': true,
                        'building-c': false,
                        'building-d': false
                    }
                },
                {
                    id: 3,
                    username: '用户二',
                    role: 'user',
                    buildingPermissions: {
                        'building-a': false,
                        'building-b': false,
                        'building-c': true,
                        'building-d': true
                    }
                }
            ],

            // 楼栋列表
            buildings: [
                { id: 'building-a', name: '楼栋A' },
                { id: 'building-b', name: '楼栋B' },
                { id: 'building-c', name: '楼栋C' },
                { id: 'building-d', name: '楼栋D' },
                { id: 'building-e', name: '楼栋E' }
            ],

            // 当前选中的用户
            selectedUser: null,

            // 控制显示哪个区域
            showAddUserForm: false,
            showEditUserForm: false,
            showUserDetail: false,
            showPermissionSettings: true,

            // 编辑用户相关
            editingUser: null,
            saving: false,

            // 表单数据
            userForm: {
                username: '',
                role: 'user',
                password: '',
                buildingPermissions: {}
            },

            // 表单验证规则
            userRules: {
                username: [
                    { required: true, message: '请输入用户名', trigger: 'blur' }
                ],
                role: [
                    { required: true, message: '请选择角色', trigger: 'change' }
                ],
                password: [
                    { required: true, message: '请输入密码', trigger: 'blur' }
                ]
            }
        }
    },

    mounted() {
        // 初始化时选中第一个用户
        if (this.users.length > 0) {
            this.selectedUser = { ...this.users[0] };
        }
    },

    methods: {
        // 处理用户选择
        handleUserSelect(user) {
            if (user) {
                // 创建用户副本以避免直接修改原数据
                this.selectedUser = JSON.parse(JSON.stringify(user));
                // 显示用户详情
                this.showUserDetail = true;
                this.showAddUserForm = false;
                this.showEditUserForm = false;
                this.showPermissionSettings = false;
            }
        },

        // 添加用户
        addUser() {
            this.showAddUserForm = true;
            this.showEditUserForm = false;
            this.showUserDetail = false;
            this.showPermissionSettings = false;
            this.editingUser = null;
            
            // 初始化表单数据
            this.userForm = {
                username: '',
                role: 'user',
                password: '',
                buildingPermissions: {}
            };
            
            // 初始化楼宇权限
            this.buildings.forEach(building => {
                this.$set(this.userForm.buildingPermissions, building.id, false);
            });

            // 清除表单验证状态
            this.$nextTick(() => {
                if (this.$refs.addUserForm) {
                    this.$refs.addUserForm.clearValidate();
                }
            });
        },

        // 编辑用户
        editUser(user) {
            this.showAddUserForm = false;
            this.showEditUserForm = true;
            this.showUserDetail = false;
            this.showPermissionSettings = false;
            this.editingUser = user;
            
            // 填充表单数据
            this.userForm = {
                username: user.username,
                role: user.role,
                password: '',
                buildingPermissions: user.role === 'user' ? { ...user.buildingPermissions } : {}
            };

            // 清除表单验证状态
            this.$nextTick(() => {
                if (this.$refs.editUserForm) {
                    this.$refs.editUserForm.clearValidate();
                }
            });
        },

        // 处理角色变更
        handleRoleChange(role) {
            if (role === 'user' && !this.userForm.buildingPermissions) {
                this.$set(this, 'userForm.buildingPermissions', {});
                this.buildings.forEach(building => {
                    this.$set(this.userForm.buildingPermissions, building.id, false);
                });
            }
        },

        // 保存用户
        saveUser() {
            let formRef = this.editingUser ? this.$refs.editUserForm : this.$refs.addUserForm;
            formRef.validate((valid) => {
                if (valid) {
                    if (this.editingUser) {
                        // 编辑用户
                        const index = this.users.findIndex(u => u.id === this.editingUser.id);
                        if (index !== -1) {
                            this.users[index].username = this.userForm.username;
                            this.users[index].role = this.userForm.role;
                            
                            // 更新楼宇权限（仅对普通用户）
                            if (this.userForm.role === 'user') {
                                this.users[index].buildingPermissions = { ...this.userForm.buildingPermissions };
                            } else {
                                // 管理员不需要楼宇权限
                                this.users[index].buildingPermissions = {};
                            }
                        }
                        this.$message.success('用户更新成功');
                    } else {
                        // 添加新用户
                        const newUser = {
                            id: Date.now(), // 简单ID生成
                            username: this.userForm.username,
                            role: this.userForm.role,
                            buildingPermissions: {}
                        };

                        // 如果是普通用户，设置楼宇权限
                        if (this.userForm.role === 'user') {
                            newUser.buildingPermissions = { ...this.userForm.buildingPermissions };
                        }

                        this.users.push(newUser);
                        this.$message.success('用户添加成功');
                    }

                    // 重置状态
                    this.showAddUserForm = false;
                    this.showEditUserForm = false;
                    this.showUserDetail = true;
                    this.showPermissionSettings = false;
                }
            });
        },

        // 删除用户
        deleteUser(user) {
            if (user.username === 'admin') {
                this.$message.warning('不能删除管理员账户');
                return;
            }

            this.$confirm(`确定要删除用户 "${user.username}" 吗？`, '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                const index = this.users.findIndex(u => u.id === user.id);
                if (index !== -1) {
                    this.users.splice(index, 1);
                    if (this.selectedUser && this.selectedUser.id === user.id) {
                        this.selectedUser = this.users.length > 0 ? { ...this.users[0] } : null;
                    }
                    this.$message.success('用户删除成功');
                }
            }).catch(() => {
                // 取消删除
            });
        },

        // 保存权限设置
        savePermissions() {
            if (!this.selectedUser) return;

            this.saving = true;

            // 模拟保存过程
            setTimeout(() => {
                // 更新用户列表中的权限信息
                const index = this.users.findIndex(u => u.id === this.selectedUser.id);
                if (index !== -1) {
                    this.users[index] = { ...this.selectedUser };
                }

                this.saving = false;
                this.$message.success('权限保存成功');
            }, 800);
        }
    }
}
</script>

<style scoped>
.permission-management.tech-main {
    padding: 10px 20px;
    /* 减少顶部padding */
    height: 100%;
    box-sizing: border-box;
    background: transparent;
    color: #e0e0e0; /* 添加默认文本颜色 */
}

.page-title.tech-title {
    font-size: 24px;
    margin: 10px 0 15px 0;
    /* 减少margin */
    color: #303133;
}

.content-container.tech-grid {
    display: flex;
    gap: 20px;
    height: calc(100% - 50px);
    /* 调整高度计算 */
}

/* 卡片通用样式 */
.tech-card {
    background: rgba(13, 33, 63, 0.7);
    border: 1px solid rgba(64, 140, 229, 0.6);
    border-radius: 8px;
    box-shadow:
        0 4px 16px rgba(0, 0, 0, 0.3),
        0 0 10px rgba(64, 140, 229, 0.3),
        inset 0 0 10px rgba(64, 140, 229, 0.2);
    backdrop-filter: blur(3px);
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
    color: #e0e0e0; /* 添加默认文本颜色 */
}

.tech-card::before {
    content: "";
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(100, 181, 246, 0.2), transparent);
    transition: 0.5s;
}

.tech-card:hover::before {
    left: 100%;
}

.tech-card:hover {
    transform: translateY(-2px);
    box-shadow:
        0 6px 20px rgba(0, 0, 0, 0.4),
        0 0 15px rgba(64, 140, 229, 0.5),
        inset 0 0 15px rgba(64, 140, 229, 0.3);
}

.user-list-section.tech-card {
    width: 40%;
    /* 固定宽度为500px */
    padding: 15px;
    /* 减少padding */
}

.add-user-section.tech-card,
.edit-user-section.tech-card,
.user-detail-section.tech-card,
.permission-section.tech-card,
.no-selection.tech-card {
    width: 40%;
    /* 固定宽度为500px */
    padding: 15px;
    /* 减少padding */
    display: flex;
    flex-direction: column;
}

.section-header.tech-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
    /* 减少margin */
    padding-bottom: 10px;
    /* 减少padding */
    border-bottom: 1px solid rgba(64, 140, 229, 0.4);
}

.section-header.tech-header h3 {
    margin: 0;
    color: #64b5f6;
    font-size: 18px;
    font-weight: 500;
    letter-spacing: 1px;
}

.tech-button {
    background: linear-gradient(135deg, #3498db, #2c3e50);
    border: none;
    color: white;
    border-radius: 4px;
    transition: all 0.3s ease;
    box-shadow: 0 3px 10px rgba(52, 152, 219, 0.3);
    padding: 8px 15px;
    /* 调整按钮padding */
    font-size: 12px;
    /* 调整按钮字体大小 */
}

.tech-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 15px rgba(52, 152, 219, 0.4);
}

.tech-button.secondary {
    background: linear-gradient(135deg, #7f8c8d, #95a5a6);
}

.tech-button.success {
    background: linear-gradient(135deg, #27ae60, #2ecc71);
}

.table-container {
    border-radius: 8px;
    overflow: hidden;
    background: transparent;
    /* 添加透明背景 */
    color: #e0e0e0;
}

.tech-table {
    background: transparent;
    /* 修改为透明背景 */
    color: #e0e0e0;
}

.permission-management :deep(.tech-table .el-table__header th) {
    background: linear-gradient(to bottom, rgba(30, 60, 90, 0.8), rgba(20, 40, 60, 0.6));
    color: #64b5f6;
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 13px;
}

.permission-management :deep(.tech-table .el-table__body td) {
    background: #fafafa;
    /* 表格单元格背景透明 */
    color: #333; /* 确保表格内文本有明确颜色 */
}

.permission-management :deep(.tech-table .el-table__row:hover) {
    background: rgba(50, 50, 80, 0.5);
    /* 保持悬停效果但降低不透明度 */
}

.tech-table-cell {
    color: #e0e0e0;
}

.action-buttons {
    display: flex;
    gap: 5px;
}

.tech-button-mini {
    padding: 4px 8px;
    /* 调整小按钮padding */
    font-size: 12px;
}

.tech-button-mini.danger {
    background: linear-gradient(135deg, #c0392b, #e74c3c);
}

.permission-content.tech-content,
.user-form-content.tech-content,
.user-detail-content.tech-content {
